import { combineReducers } from 'redux'
import {
  RECIEVE_POKEMONS,
  REQUEST_POKEMONS
} from '../actions/actions'

function pokemons(
  state = [],
  action
) {
  switch (action.type) {
    case REQUEST_POKEMONS:
      return state
    case RECIEVE_POKEMONS:
      return action.pokemons
    default:
      return state
  }
}

const rootReducer = combineReducers({
  pokemons
})

export default rootReducer



