//import fetch from 'cross-fetch'

export const REQUEST_POKEMONS = 'REQUEST_POKEMONS'
export const RECIEVE_POKEMONS = 'RECIEVE_POKEMONS'
export const REQUEST_POKEMON = 'REQUEST_POKEMON'
export const RECIEVE_POKEMON = 'RECIEVE_POKEMON'

function requestPokemons() {
  return {
    type: REQUEST_POKEMONS
  }
}

function receivePokemons(pokemons) {
  return {
    type: RECIEVE_POKEMONS,
    pokemons: pokemons
  }
}

function requestPokemon() {
  return {
    type: REQUEST_POKEMON
  }
}

function receivePokemon(pokemon) {
  return {
    type: RECIEVE_POKEMON,
    pokemon: pokemon
  }
}

export function fetchPokemons() {
  return dispatch => {
    dispatch(requestPokemons())
    return fetch(`https://pokeapi.co/api/v2/pokemon/?limit=20&offset=0`)
      .then(response => response.json())
      .then(json => dispatch(receivePokemons(json.results.map(i => i.name))))
  }
}

export function fetchPokemon(pokemonID) {
  return dispatch => {
    dispatch(requestPokemon())
    return fetch(`https://pokeapi.co/api/v2/pokemon/`+pokemonID)
      .then(response => response.json())
      .then(json => dispatch(receivePokemon(json.results.map(i => i.name))))
  }
}



